import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddConstructionStageComponent } from './add-construction-stage.component';

const routes: Routes = [
  {
    path: '',
    component: AddConstructionStageComponent,
    data: {
      title: 'Add Construction-Stage'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ConstructionStageCrudRoutingModule {}



