import { NgModule } from '@angular/core';
import { CommonModule }      from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { AddConstructionStageComponent } from './add-construction-stage.component';
import { ConstructionStageCrudRoutingModule } from './add-construction-stage-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    ConstructionStageCrudRoutingModule,
    DialogModule,
    EditorModule,
	  BusyModule
  ],
  declarations: [
    AddConstructionStageComponent
  ]
})
export class AddConstructionStageModule { }



