import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi ,CityApi,Construction_stage_masterApi} from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'add-construction-stage.component.html'
})
export class AddConstructionStageComponent {
	@ViewChild('constrctnStgName') name: NgModel;
	display: boolean = false; result: any;
	submitted: boolean = false;
	constrctnStgName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	data: any;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;
	constStgStatus:boolean = false;
	constctnStagePrjtType: string;

	constructor(private loopAuth: LoopBackAuth, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService,private cityApi:CityApi,private constrctnStgApi:Construction_stage_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		
		this.rolename = this.userDetails.userDet.roles;
	}


	async submit(form) {
		this.submitted = true;
		let validFlag = true;
		var date = new Date();
		var currentDate = date.getTime();

		if (form.valid) {

			//function call to change base64 format data to Image
			var dataArrObj = {
				"construction_stage_name": this.capital_letter(this.constrctnStgName),
				"status": (this.constStgStatus)?true:false,
				"created_date":currentDate,
				"updated_date":currentDate,
				"project_status":this.constctnStagePrjtType
			}

			this.loading = true;
			this.ifNameExist(dataArrObj).then((total)=>{
				if(total > 0){
					this.loading = false;
					this.result = "Construction-stage name already exist";
					this.display = true;
				}else{
					this.constrctnStgApi.upsert(dataArrObj).subscribe((data)=>{
						this.loading = false;
						this.result = "success";
						this.display = true;
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
				}
			},(err)=>{
				this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
			});

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				
				let constrctStgNameString : string = dataObj.construction_stage_name;
				let constrctStgPrjtTypString : string = dataObj.project_status;
				

				this.constrctnStgApi.getConstructionStageCountName({ "construction_stage_name": constrctStgNameString,"project_status":constrctStgPrjtTypString}).subscribe((data)=>{
					resolve(data.result[0].total);
				},(err)=>{
					reject(err);
				})

			}catch(err){
				reject(err);
			}
		});
		
		
	}

	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}

	ngOnInit(): void {

	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["construction-stage"]);
	}

	
}


